.Products {
  padding: 80px 10px 10px 20px;
  width: 100%;
  height: 100vh;
  background: rgb(247, 246, 246);
  display: flex;
}
.Products-Filters {
  display: none;
  width: 20%;
  height: 100%;
  padding: 10px;
  background: white;
  box-shadow: 0px 10px 10px rgb(204, 202, 202);
  margin-right: 1rem;
}
.filter-head {
  padding: 10px 0;
  font-size: 20px;
  border-bottom: 1px solid rgb(228, 225, 225);
}
.filter-categoryHead {
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 500;
}
.filter-category {
  padding: 10px 20px 0px;
  font-size: 15px;
}
.category {
  padding: 15px 0;
  border-bottom: 1px solid rgb(228, 224, 224);
}
.Price {
  padding: 15px 0;
  border-bottom: 1px solid rgb(211, 204, 204);
}
.priceHead {
  text-transform: uppercase;
}
.pricerange {
  width: 100%;
  margin-top: 10px;
}
.price-input {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.price-input > p {
  font-size: 15px;
  color: gray;
}
.pricebox {
  width: 100px;
  padding: 5px;
}
.brand {
  padding: 20px 0;
  border-bottom: 1px solid rgb(207, 205, 205);
  text-transform: uppercase;
  font-size: 15px;
}
/* ------------------------------------------------- */

.Products-items {
  width: 100%;
  height: 100%;
  background: white;
  padding: 15px;
}
.totalresults {
  font-size: 15px;
  letter-spacing: 1px;
}
.sortby {
  padding: 15px 0;
  display: flex;
}
.sortby > p {
  font-size: 15px;
}
.sortitems {
  display: flex;
  padding-left: 20px;
}
.sortitem {
  list-style: none;
  padding: 0 10px;
  font-size: 14px;
  font-weight: 300;
  cursor: pointer;
}
